import { useContext, useEffect, useState } from 'react';
import { Container, Card, CardContent, Typography, Grid, Box, Button, CircularProgress } from '@mui/material';
import BlogService from '../../services/BlogService';
import { useNavigate } from 'react-router-dom';
import { SettingsContext } from '../../context/SettingsContext';
import { useUser } from '../../context/UserContext';

const BlogCard = ({ sx }) => {
    const { language, themeMode } = useContext(SettingsContext);
    const { userState } = useUser();
    const [blogs, setBlogs] = useState([]);
    const [total, setTotal] = useState(0);
    const [loading, setLoading] = useState(true); // 初始加载
    const [loadingMore, setLoadingMore] = useState(false); // 加载更多状态
    const [error, setError] = useState(null);
    const [page, setPage] = useState(1);
    const [pageSize] = useState(3); // 每页显示的数量
    const [hasMore, setHasMore] = useState(true); // 是否还有更多数据
    const navigate = useNavigate();

    // 初始加载
    useEffect(() => {
        const fetchInitialData = async () => {
            try {
                setLoading(true);
                setError(null);
                const metadata = await BlogService.getMeta(userState.access, 1, pageSize);
                setBlogs(metadata.data);
                setTotal(metadata.total);
                setPage(1);
                // 检查是否还有更多数据
                setHasMore(metadata.data.length < metadata.total);
                setLoading(false);
            } catch (error) {
                console.error('Error fetching metadata:', error);
                setError(language === 'cn' ? '404 \n 访问失败 \n 无法加载博客' : '404 \n Not Found \n Fail to load blogs');
                setLoading(false);
            }
        };
        fetchInitialData();
    }, [language, userState.access, pageSize]);

    // 加载更多数据
    const loadMore = async () => {
        try {
            setLoadingMore(true);
            const nextPage = page + 1;
            const metadata = await BlogService.getMeta(userState.access, nextPage, pageSize);
            
            // 合并新数据到现有数据
            setBlogs(prevBlogs => [...prevBlogs, ...metadata.data]);
            setPage(nextPage);
            
            // 检查是否还有更多数据
            const totalLoaded = blogs.length + metadata.data.length;
            setHasMore(totalLoaded < metadata.total);
            
            setLoadingMore(false);
        } catch (error) {
            console.error('Error loading more blogs:', error);
            setLoadingMore(false);
            // 可以选择显示加载更多失败的提示
        }
    };

    const handleCardClick = (id) => {
        navigate(`/blog/${id}`);
    };

    return (
        <Container maxWidth="md" sx={{ ...sx }}>
            {loading ? ( // 初始加载状态
                <Box display="flex" justifyContent="center" mt={3.5}>
                    <CircularProgress />
                </Box>
            ) : error ? ( // 加载失败状态
                <Typography variant="h6" color="error" align="center" mt={3.5} style={{ whiteSpace: "pre-line" }}>
                    {error}
                </Typography>
            ) : (
                <>
                    <Grid container spacing={2} direction="column">
                        {blogs.map((blog) => (
                            <Grid item key={blog.id}>
                                <Card
                                    sx={{
                                        background: 'transparent',
                                        borderBottom: `1px solid ${themeMode === 'light' ? '#ccc' : '#555'}`,
                                        borderRadius: 0,
                                        boxShadow: 'none',
                                        cursor: 'pointer',
                                    }}
                                    onClick={() => handleCardClick(blog.id)}
                                >
                                    <CardContent>
                                        <Typography
                                            variant="h5"
                                            component="div"
                                            gutterBottom
                                            sx={{ fontFamily: 'Georgia, serif', fontWeight: 'bold' }}
                                        >
                                            {language === 'cn' ? blog.cn_title : blog.en_title}
                                        </Typography>
                                        <Typography
                                            variant="body1"
                                            color="text.primary"
                                            sx={{ fontFamily: 'Georgia, serif' }}
                                        >
                                            {language === 'cn' ? blog.cn_summary : blog.en_summary}
                                        </Typography>
                                        <Box display="flex" justifyContent="flex-end" mt={2} sx={{ fontFamily: 'Georgia, serif' }}>
                                            <Typography variant="body1" color="text.secondary">
                                                {
                                                new Date(blog.publish_date).toLocaleDateString(language === 'cn' ? 'zh-CN' : 'en-GB', {
                                                    day: '2-digit',
                                                    month: language === 'cn' ? '2-digit' : 'short',
                                                    year: 'numeric',
                                                    })
                                                }
                                            </Typography>
                                        </Box>
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                    
                    {/* Load More 按钮 */}
                    {hasMore && (
                        <Box display="flex" justifyContent="center" mt={2}>
                            <Button
                                onClick={loadMore}
                                disabled={loadingMore}
                                color={themeMode === 'light' ? 'primary' : 'info'}
                                variant="text"
                                sx={{
                                    fontFamily: 'Georgia, serif',
                                    textTransform: 'none',
                                    fontSize: '1rem',
                                    '&:hover': {
                                        backgroundColor: 'transparent',
                                        color: themeMode === 'light' 
                                            ? '#0d47a1'  // 在亮色模式下使用更深的主色调
                                            : 'info.dark',    // 在暗色模式下使用更深的info色调
                                    },
                                    '&.Mui-disabled': {
                                        color: 'text.disabled',
                                    },
                                }}
                            >
                                {loadingMore ? (
                                    <CircularProgress size={20} color="info" />
                                ) : (
                                    language === 'cn' ? '加载更多' : 'Load More'
                                )}
                            </Button>
                        </Box>
                    )}
                    
                    {/* 显示总数信息 */}
                    <Box display="flex" justifyContent="center" mt={2} mb={2}>
                        <Typography variant="body" color="text.secondary" sx={{ fontFamily: 'Georgia, serif' }}>
                            {language === 'cn' 
                                ? `已显示 ${blogs.length} / ${total} 篇文章`
                                : `Showing ${blogs.length} / ${total} articles`
                            }
                        </Typography>
                    </Box>
                </>
            )}
        </Container>
    );
};

export default BlogCard;