import { useContext, useEffect, useState } from 'react';
import { Box, Fab, Container, Snackbar, Alert } from '@mui/material';
import CustomAppBar from '../../components/shared-components/AppBar/AppBar';
import { SettingsContext } from '../../context/SettingsContext'; 
import BlogCard from '../../components/blog-components/BlogCard';
import { useUser } from '../../context/UserContext'; 
import AddIcon from '@mui/icons-material/Add'; 
import { useNavigate, useSearchParams } from 'react-router-dom'; 
import Footer from '../../components/shared-components/Footer';

import { motion } from 'framer-motion';

const fadeInVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0, transition: { duration: 0.5 } },
};


const Blogs = () => {
  const { language, themeMode, toggleLanguage, toggleTheme } = useContext(SettingsContext); // 获取当前语言和主题
  const { userState } = useUser(); // 从 UserContext 获取用户状态
  const navigate = useNavigate(); // 初始化 navigate
  const [searchParams, setSearchParams] = useSearchParams();

  // Snackbar 状态
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  // 检查 URL 参数
  useEffect(() => {
    const updated = searchParams.get('updated');
    if (updated === 'true') {
      setSnackbarMessage(language === "cn" ? "博客已成功发布！" : "Blog added successfully!");
      setSnackbarOpen(true);
      
      // 清除 URL 参数，避免刷新页面时重复显示
      searchParams.delete('updated');
      setSearchParams(searchParams, { replace: true });
    }
  }, [searchParams, setSearchParams, language]);

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const handleAddBlog = () => {
    navigate('/addblog'); // 跳转到 AddBlogPage
  };


  return (
    <Box>
      <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh'}}>
        <CustomAppBar />
        <motion.div initial="hidden" animate="visible" variants={fadeInVariants}>
          <Container
            maxWidth="lg"
            sx={{
              display: 'flex',
              flexDirection: 'column',
              mt: "100px",
            }}
          >
            <BlogCard />
          </Container>
        </motion.div>
        {/* 右下角固定按钮，只有 isStaff 为 true 时显示 */}
        {userState.is_staff && (
          <Fab
            color="primary"
            aria-label="add"
            onClick={handleAddBlog}
            sx={{
              position: 'fixed', 
              bottom: '50px',
              right: 'calc((100% - 1200px) / 2 + 20px)',
              '@media (max-width: 1280px)': {
                right: '30px',
              },
            }}
          >
            <AddIcon />
          </Fab>
        )}
      </Box>

      {/* 下部 Footer */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          backgroundColor: 'background.default',
          borderTop: (theme) => ({
            xs: 'none',
            md: `1px solid ${theme.palette.divider}`,
          }),
          maxWidth: (theme) => theme.breakpoints.values.lg,
          width: '100%',
          mx: 'auto',
        }}
      >
        <Footer />
      </Box>

      {/* Success Snackbar */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1000}
        onClose={handleSnackbarClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert 
          severity="success" 
          sx={{ width: "100%" }}
        >
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Blogs;